body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  font-family: 'Abel', sans-serif;
}
html {
  scroll-behavior: smooth; 
}
body {
  background: #333;
  color: #fff;
}
.navigation-menu {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  padding:20px;
  margin:0;
}
.navigation-menu li {
  list-style-type: none;
  width:120px;
  height:40px;
  line-height:40px;
  margin: 0 20px;
  text-align: center;
  text-transform: uppercase;
  background-color: rgb(68, 68, 68);
  border-radius: 5px;
}
.navigation-menu li:first-child {
  margin-left: 0;
}
.navigation-menu li:last-child {
  margin-right: 0;
}
.navigation-menu__link {
  color: rgb(255, 255, 255);
  text-decoration: none;
  transition: color 2s;
}
.navigation-menu__link:hover {
  color: rgb(42, 127, 255);
}

.react-page__project-container,
.js-page__project-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
  height: 600px;
  -webkit-align-content: center;
          align-content: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  /* border: 1px solid red; */
  background-size: cover;
  background: url('https://awaywestray.com/wp-content/uploads/2018/05/stanley-dai-242205-unsplash.jpg');
  background-size: 0% 20%;
  /* background-blend-mode: soft-light; */
}
.react-page, .js-page {
  background: url('https://awaywestray.com/wp-content/uploads/2018/05/stanley-dai-242205-unsplash.jpg');

  padding-top: 20px;
  /* background: #fff; */
}
.react-page h2, .js-page h2 {
  font-size: 36px;
  /* color:rgb(0, 63, 136); */
  color:#fff;
  margin:0;
  font-weight:normal;
}
.react-page__project, .js-page__project {
  width: 400px;
  height: 400px;
  margin:20px;
  position:relative;
  font-size: 28px;
}
/* react page */
.react-page a:first-child .react-page__project {
  background: url('https://raw.githubusercontent.com/Valeria38/portfolio/develop/src/img/reactpage/tictactoe.jpg') no-repeat;
  background-size: cover;
}
.react-page a:nth-child(2) .react-page__project{
  background: url('https://raw.githubusercontent.com/Valeria38/portfolio/develop/src/img/reactpage/todo.jpg') no-repeat;
  background-size: cover;
}
.react-page a:last-child .react-page__project{
  background: url('https://raw.githubusercontent.com/Valeria38/portfolio/develop/src/img/reactpage/forecast.jpg') no-repeat;
  background-size: cover;
}
/* js page */
.js-page__project-container {
  height: 900px;
}
.js-page a:first-child .js-page__project {
  background: url('https://raw.githubusercontent.com/Valeria38/portfolio/develop/src/img/jspage/tictactoe.jpg') no-repeat;
  background-size: cover;
}
.js-page a:nth-child(2) .js-page__project{
  background: url('https://raw.githubusercontent.com/Valeria38/portfolio/develop/src/img/jspage/15puzzle.jpg') no-repeat;
  background-size: cover;
}
.js-page a:nth-child(3) .js-page__project {
  background: url('https://raw.githubusercontent.com/Valeria38/portfolio/develop/src/img/jspage/calendar.jpg') no-repeat;
  background-size: cover;
}
.js-page a:nth-child(4) .js-page__project {
  background: url('https://raw.githubusercontent.com/Valeria38/portfolio/develop/src/img/jspage/gallery.jpg') no-repeat;
  background-size: cover;
}
.js-page a:nth-child(5) .js-page__project {
  background: url('https://raw.githubusercontent.com/Valeria38/portfolio/develop/src/img/jspage/clock.jpg') no-repeat;
  background-size: cover;
}
.js-page a:nth-child(6) .js-page__project {
  background: url('https://raw.githubusercontent.com/Valeria38/portfolio/develop/src/img/jspage/tabs.jpg') no-repeat;
  background-size: cover;
}
.js-page__project, .react-page__project {
  transition: -webkit-transform 2s;
  transition: transform 2s;
  transition: transform 2s, -webkit-transform 2s;
}
.js-page__project:hover, .react-page__project:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

/*  */
.react-page .layer, .js-page .layer {
  display: none;
  background:rgba(0, 0, 0, .5);
  position:absolute;
  top: 0;
  left: 0;
  height:100%;
  width:100%;
}

.react-page a .react-page__project:hover .layer,
.js-page a .js-page__project:hover .layer {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
}

.react-page__project a,
.js-page__project a {
  color:white;
  margin:10px;
  text-decoration: none;
}
.react-page__project a:hover,
.js-page__project a:hover {
  color: tomato;
}

.homepage {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  height: 480px;
  padding:20px;
  background: url('https://cdn-images-1.medium.com/max/1600/1*dlN-vF1YJ7ZpOrx8rOGBEQ.jpeg') no-repeat;
  background-size: cover;
  margin-top:20px;
  /* border: 1px solid royalblue; */
}
.homepage__photo {
  width:200px;
  height:200px;
  border-radius: 50%;
  background: url('https://raw.githubusercontent.com/Valeria38/portfolio/develop/src/img/homepage/me.jpg') no-repeat;
  background-size: cover;
}
/* .homepage__photo:hover {

} */

.homepage__info p {
  font-size:32px;
  color: rgb(255, 255, 255);
}
.homepage__skills {
  display: -webkit-flex;
  display: flex;
}
.homepage__link,
.homepage__tech-skills {
  padding:40px;
  font-size:32px;
  background: rgb(253, 244, 212);
  color:rgb(148, 132, 111);
  height:400px;
  width:50%;
}
.homepage__link .link {
  text-decoration: none;
  color: rgb(137, 172, 204);
}
.homepage__tech-skills {
  background: rgb(211, 205, 191);
}
.homepage__tech-skills li:first-child {
  font-weight: bold;
}
.homepage__menu {
  list-style-type: none;
  padding: 20px;
  margin:0;
}
.homepage__scroll {
  width:100%;
  height:80px;
  /* border: 1px solid royalblue; */
}
.homepage__arrow-down {
  margin-top: 20px;
  display: block;
  color: rgb(112, 112, 112);
  -webkit-transform: rotate(135deg);
          transform: rotate(135deg);
}
.homepage__arrow-down:hover {
  color: rgb(184, 184, 184);
}
.homepage__arrow-down::before {
  border-style: solid;
  border-width: 0.35em 0.35em 0 0;
  content: '';
  display: inline-block;
  height: 40px;
  vertical-align: top;
  width: 40px;
  box-sizing: border-box;
}
.homepage__link{
  background:url('http://jmbliss.com/wp-content/uploads/2017/08/slide444.jpg');
  background-size: 100% 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-end;
          align-items: flex-end;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  font-weight:bold;
  font-size:38px;
  text-decoration: none;
  color: rgb(105, 139, 168);
}
.homepage__link:hover {
  color: rgb(76, 127, 173);
  cursor: pointer;
}
