@media screen and (max-width: 1450px) {
  /* .react-page__project, */
  .js-page__project {
    width:300px;
    height: 300px;
    font-size: 26px;
  }
  .react-page__project {
    width:350px;
    height:350px;
    font-size: 28px;
  }
  .react-page__project-container {
    height: 600px;
  }
  .js-page__project-container {
    height: 700px;
  }
}
@media screen and (max-width: 1230px) {
  .homepage {
    height:700px;
  }
}
@media screen and (max-width: 1187px) {
  .react-page__project-container {
    height: 800px;
  }
}

@media screen and (max-width: 1050px) {
  .homepage {
    height:780px;
  }
  .homepage p {
    font-size: 30px;
  }
  .homepage__skills {
    -webkit-flex-direction: column-reverse;
            flex-direction: column-reverse;
  }
  .homepage__link,
  .homepage__tech-skills {
    width: auto;
  }
  .react-page__project-container {
    height:900px;
  }
  .js-page__project-container {
    height: 740px;
  }
}

@media screen and (max-width: 1036px) {
  .js-page__project-container {
    height: 1100px;
  }
}

@media screen and (max-width: 820px) {
  .react-page__project-container {
    height: 900px;
  }
  /* .js-page__project-container {
    height: 980px;
  } */
}
@media screen and (max-width: 800px) {
  .react-page__project-container {
    height: 1200px;
  }
}

@media screen and (max-width: 750px) {
  .homepage {
    height: 880px;
  }
  .homepage p {
    font-size: 30px;
  }
}

@media screen and (max-width: 700px) {
  .navigation-menu li {
    margin:0px;
    border-radius: 0;
  }
  .react-page__project-container {
    height: 1350px;
  }
  .react-page__project,
  .js-page__project {
    width: 400px;
    height:400px;
    font-size: 28px;
  }
  .js-page__project-container {
    height: 2700px;
  }
}

@media screen and (max-width: 600px) {
  .homepage {
    height: 840px;
  }
  .homepage p {
    font-size:28px;
  }
  .homepage__link,
  .homepage__tech-skills {
    height:360px;
    font-size:28px;
    padding:10px;
    overflow:hidden;
  }
}

